import style from '../scss/readme.module.scss';
import { Link, Markdown, useBook, useFile, useTitle } from 'topwrite';

export default function Readme() {

    const file = useFile();
    const title = useTitle();
    const { summary } = useBook();
    const path = summary.getFirstPath();

    return <div className={style.readme}>
        <header>
            <div className={style.container}>
                <div className={style.title}>{title}</div>
                <div className={style.subTitle}></div>
                <div className={style.buttons}>
                    {path && <Link className={style.button} to={path}>Get Started</Link>}
                </div>
            </div>
        </header>
        <div className={style.container}>
            <Markdown aside={false}>{file.content}</Markdown>
        </div>
    </div>;
}
